"use client";

import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "@/redux/app/store";
import {fetchBarbers} from "../../redux/features/BarberSlice";


const BarberManagement: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();

  const { barbers, loading, error } = useSelector((state: RootState) => state.barber);
  console.log(barbers,"hello")
  
  useEffect(() => {
    dispatch(fetchBarbers());
  }, [dispatch]);

  return (
    <div className="w-full px-4">
      {/* Loading / Error */}
      {loading && <p className="text-center text-yellow-500">Loading...</p>}
      {error && <p className="text-center text-red-500">{error}</p>}

      {/* Grid */}
      <div className="grid grid-cols-2 gap-4 sm:grid-cols-3 md:grid-cols-4">
        {barbers.map((barber) => (
          <div key={barber._id} className="flex justify-center">
            <div className="relative w-32 p-3 overflow-hidden bg-black bg-opacity-80 border border-gold rounded-lg shadow-lg sm:w-40 backdrop-blur-md">
              <div className="p-2 text-center text-gold">
                <h6 className="text-sm font-semibold">{barber.name}</h6>
                <p className="text-sm text-gold">{barber.specialization}</p>
                <button className="w-full px-3 py-1 mt-2 text-xs font-semibold text-black bg-gold rounded-md">
                  Book Now
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default BarberManagement;
